.tool_panel .tool_items {
  color: #212121;
  background-color: #fff;
  padding-left: 20px;
}
.tool_panel .tool_items .kd-space {
  row-gap: calc(var(--rgap)/2);
}
.tool_panel .tool_items .tool_item .tool_item_li {
  width: 157px;
  height: 32px;
  line-height: 32px;
  margin-right: 5px;
  margin-bottom: 14px;
  padding: 7px 15px;
  background: #F5F5F5;
  border: 1px solid #F5F5F5;
  border-radius: 2px;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  letter-spacing: 0;
  font-weight: 400;
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: inherit;
}
.tool_panel .tool_items .tool_item .tool_item_li:hover {
  color: var(--theme-color);
  background: #F1F6FD;
  border: 1px solid rgba(39, 111, 245, 0.3);
}
.tool_panel {
  padding-left: 20px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.tool_panel .tool_item_li {
  width: 157px;
  height: 32px;
  line-height: 32px;
  margin-right: 16px;
  margin-bottom: 14px;
  padding: 8px 12px;
  cursor: pointer;
  background: #F5F5F5;
  border: 1px solid #F5F5F5;
  border-radius: 2px;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  letter-spacing: 0;
  font-weight: 400;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  align-items: center;
  vertical-align: middle;
}
.tool_panel .tool_item_li .tool_item_li_text {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  color: #212121;
}
.tool_panel .tool_item_li .tool_item_li_icon {
  color: #999;
}
.tool_panel .tool_item_li:hover {
  color: var(--theme-color);
  background: #F1F6FD;
  border: 1px solid rgba(39, 111, 245, 0.3);
}
.tool_panel .tool_item_li:hover .tool_item_li_icon {
  color: var(--theme-color);
}
